@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap);
* {
    /* margin: 0; */
    padding: 0;
    outline: 0;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    font-family: 'Roboto', sans-serif;
    
  }

  body {
      margin: 0;
  }

.nav-header {
  
    min-height: 100%;
    height: 55px;
    background-color: #7530c5;
   
}  

a {
    text-decoration: none;
}



.btn-global {
    padding: 10px 15px;
    width: 320px;
    height: 45px;
    border-radius: 8px;
    border: none;
  
    background-color: #6f3dc0;
    color: #fff;
    font-weight: 700;
    font-size: 14px;
    margin: 25px 0 20px;
    cursor: pointer;
    transition: all 0.25s linear;
  }
  
  .btn-global:hover {
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
    transform: scale(1.03);
  }




#content {
    min-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    height: 100vh;
  
    min-height: 830px;
}

.main-content {
  display: flex;
  justify-content: center;
  margin: 40px 0;
  width: 100%;



}



.side-svg {
    
    width: 600px;
    display: flex;
    justify-content: center;

}



.side-svg > img {
    width: 550px;
}



@media (max-width: 1300px) 
{

    .side-svg  {
        position: absolute;
        left: calc(50% - 300px);
        z-index: -1;
        
       

    }


}


@media (max-width: 730px) 
{

    .side-svg {
      left: 0;
      width: 100%;
     
    }

    .side-svg > img {
       display: none;
    }
    
   
}

.side-form {
    
   
    display: flex;

}

.side-form > form {
   
    width: 600px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    

}

.form-input {
    margin: 10px 0;
    border-radius: 8px;
    padding: 10px 15px;
    border: 1px solid #b9b9b9;
    color: #535353;
    width: 300px;
    height: 45px;
    font-size: 15px;
}

.form-input:focus {
    background-color: #f0f0f0;
    transition: 0.3s;
  }


  .form-input:-webkit-autofill, .form-input:focus:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 100px #ffffff inset;
  
  }
  

  select.form-select {
    margin: 5px 0;
    border-radius: 8px;
    padding: 10px 15px;
    background-color: #f0f0f0;
    border: 1px solid #b9b9b9;
    color: #535353;
    width: 300px;
    height: 45px;
    font-size: 15px;
    transition: background 0.25s linear;
    
    cursor: pointer;
 
  }

  select option {
    color:#473d3d;
    padding: 15px;
    text-overflow:none;
    
} 

select option:hover {
    background-color: #535353;
}

  .form-select:hover {
      background-color: #f9f9f9;
  }

  .form-select:focus {
    background-color: #e9e9e9;
    transition: 0.3s;
    box-shadow: 0 0 0 3px -moz-mac-focusring;

    outline: none;
    
  }


 


  @media (max-width: 1300px) 
{

    #content {
        position: relative;
        
    }

    .main-content{
        flex-direction: column-reverse;
    
        height: 50%;
        
    }

    .side-form{
        width: 100%;
     
        height: 100%;
        background-color: rgba(255, 255, 255, 0.9);
    }
}
section.result-info {
    color: #3f3030;
   
    width: 100%;
    display: flex;
    justify-content: center;
    /* background-color: #f1f1f1; */
    margin: 20px 0;
   
  }
  
  section.result-info > .result-card {
  
    width: 330px;
    height: 220px;
    /* border-radius: 5px; */
    margin: 0 15px;
    border-bottom: 3px solid #501b81;
    background-color: #ececec;
 
  }
  
  .result-info .result-card > h1 {
    color: #ffffff;
    font-size: 1.1em;
    margin: -8px -5px;
    padding: 6px;
    background-color: #5b437a;
    box-shadow: 2px 2px 2px 1px rgba(80, 63, 83, 0.4);
    width: 150px;
    
    
    text-transform: uppercase;
  }
  
  .result-info .result-info-txt {
    
    margin: 10px 0;
  
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  
    height: calc(100% - 48px);
  }
  
  .result-info .result-info-txt h1 { 
    font-size: 1.6em;
    color: #57445a;
  }
  

  @media (max-width: 730px) 
{

    section.result-info > .result-card {
        margin: 0 10px;
        width: 180px;
        height: 150px;
    }

    section.result-info {
       
        margin: 0;

    }

    .result-info .result-card > h1 {
        font-size: 1em;
    }

    .result-info .result-info-txt h1 { 
        font-size: 1.3em;
        color: #57445a;
      }

}

